    }

    .C3-columna-2 p {
        width: 410px;
    }

    .imagenC3 {
        margin-top: 40px;
        margin-left: 35px;
        width: 390px;
        height: 390px;
    }

    .container4 {
        width: 460px;
        grid-template-columns: repeat(1, 1fr);
        gap: 20px;
        padding: 15%;
    }

    .container4 .C4-column {
        width: 320px;
    }

    .T-parrafo {
        font-size: 30px;
    }

    .container5 {
        padding-left: 20px;
    }

    .containerF5 {
        width: 460px;
    }

    .react-multi-carousel-list,
    .react-multi-carousel-track {
        width: 350px;
    }

    .containerF5 .h3-C5 {
        margin: 0px;
        text-align: center;
    }
}
